$light-yellow-color: #F3B312;
$darken-yellow-color: #FC9700;

//breakpoints
$md: 768px;
$sm: 576px;

@font-face {
  font-family: "Muller-regular";
  src: url('../fonts/Muller-Trial-Regular.woff2') format("woff2"),
  url('../fonts/Muller-Trial-Regular.woff') format("woff");
}

.body {
  box-sizing: border-box;
  overflow: hidden;

}

.main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  font-family: "Muller-regular", sans-serif;

  .main-wrapper {
    width: 700px;
    @media (max-width: $md){
      width: 100%;
      max-width: 700px;
    }
  }

  &__title {
    margin-bottom: 15px;
    font-weight: 300;
    font-size: 30px;
    letter-spacing: 0.2px;
    transform: translate(22px);
    @media (max-width: $md){
      margin: 0 auto;
      transform: translate(0);
      font-size: 25px;
      text-align: center;
      margin-bottom: 20px;
    }

    p {
      color: red;
      font-weight: 700;
    }
  }
}

.main-list {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
  width: 100%;
  list-style: none;
  transform: translate(17px);
  @media (max-width: $md) {
    display: none;
  }


  &__item {
    display: flex;
    align-items: center;
    font-size: 15px;

    span {
      color: $light-yellow-color;
    }

    img {
      margin-right: 15px;
    }
  }

  .present {
    margin-right: 35px;

    img {
      width: 40px;
    }
  }

  .time {
    margin-right: 33px;

    img {
      width: 36px;
    }
  }

  .cost {
    img {
      width: 30px;
    }
  }
}

//контур
.form-contour {
  position: relative;

  .main-bg {
    width: 100%;
    @media (max-width: $md) {
      display: none;
    }
  }
  //mobile-bg
  .mobile-bg {
    display: none;
    @media (max-width: $md) {
      display: block;
      width: 100%;
      height: 385px;
      background-color: $light-yellow-color;
    }
    img {
      position: absolute;
      width: 130px;
      bottom: 0;
      right: 16%;
    }
  }

  .decoration {
    position: absolute;
    @media (max-width: $md) {
      display: none;
    }
  }

  .employee {
    top: -50px;
    right: -63px;
    width: 261px;
  }

  .phone{
    opacity: 0;
    top: -18px;
    right: -67px;
    width: 322px;
  }

}


// content that i hide on click
.content-form,
.content {
  max-width: 345px;
  position: absolute;
  top: 25px;
  left: 110px;
  @media (max-width: $md) {
    position: absolute;
    top: 40%; left: 50%;
    transform: translate(-49%,-50%);
  }

  .title {
    font-weight: 400;
    white-space: nowrap;
    @media (max-width: $md) {
      white-space: normal;
    }
  }
  &__help-text {
    position: absolute;
    top: 20px;
    right: -135px;
    z-index: 1;
    @media (max-width: $md) {
      display: none;
    }

    max-width: 125px;
    padding: 5px;
    background-color: #fff;
    border-radius: 5px;
    font-size: 15px;
    &:after {
      content: '';
      position: absolute;
      top: 3px;
      display: block;
      width: 0;
      right: -25px;
      height: 0;
      border-left: 0 solid transparent;
      border-right: 27px solid transparent;
      border-top: 12px solid white;
      z-index: 0;
    }
    span {
      color: red;
    }
  }

  &__list {
    margin-top: 35px;
    display: grid;
    grid-template-columns: repeat(3, 20%);
    grid-row-gap: 25px;
    grid-column-gap: 70px;
    align-items: end;
    padding: 0;
    @media (max-width: $sm){
      grid-column-gap: 60px;
    }
  }

  &__list-item {
    cursor: pointer;

    img {
      &:hover {
        transform: scale(1.1);
      }
    }

    span {
      &:hover {
        transform: scale(1.1);
      }
    }

    .kia {
      width: 73px;
    }

    .mazda {
      width: 54px;
    }

    .volvo {
      width: 54px;
    }

    .ford {
      width: 80px;
    }

    .volkswagen {
      width: 54px;
    }

    .renault {
      width: 50px;
    }

    .hyundai {
      width: 70px;
    }

    .mitsubishi {
      width: 56px;
    }

    .inner-mark {
      display: flex;
      flex-direction: column;
      align-items: center;
      font-size: 18px;
    }
  }
}

.bottom-text {
  display: block;
  text-align: center;
  font-size: 14px;
  transform: translate(-50px);
  @media (max-width: $md) {
    display: none;
  }
}

//second screen
.content-form {
  display: none;

  &__inner {

  }

  &__title {
    font-weight: 400;
    letter-spacing: 0.3px;
    margin-bottom: 20px;
  }

  &__input {
    position: relative;
    display: block;
    padding: 0 10px;
    width: 100%;
    height: 38px;
    border: 2px solid $darken-yellow-color;
    border-radius: 4px;
    margin-bottom: 20px;

    &:not(:focus) + .content-form__input-cursor:before {
      display: inline-block;
      animation: cursor 1s infinite step-end;
    }
  }

  @keyframes cursor {
    50% {
      background: transparent;
    }
  }

  &__input-cursor {
    position: relative;
    &:before {
      content: '';
      display: none;
      position: absolute;
      left: 10px;
      top: -48px;
      width: 1px;
      height: 17px;
      background: #000;
      opacity: 1;
      z-index: 3;
    }
  }

  //Checkbox-list
  &__checkbox-list {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    gap: 10px;
  }

  &__checkbox-list-item {
    display: flex;
    align-items: center;
  }

  &__checkbox-list-label {
    cursor: pointer;
  }

  &__checkbox {
    appearance: none;
    width: 20px;
    height: 20px;
    margin-right: 5px;
    background: url(../img/empty-checkbox.svg) no-repeat;
    background-size: 20px 20px;

    &:checked {
      background: url(../img/checked-checkbox.svg) no-repeat;
      background-size: 20px 20px;
    }
  }

  //  buttons
  &__footer {
    display: flex;
  }

  &__footer-button {
    width: 200px;
    height: 38px;
    margin-right: 10px;
    border-radius: 4px;
    background-color: $darken-yellow-color;
    color: white;
    font-weight: 300;
    font-size: 14px;
    cursor: pointer;
    @media (max-width: $sm) {
      width: 140px;
      font-size: 12px;
    }

    &:hover {
      font-weight: 700;
      background-color: red;
    }

    &:last-child {
      margin-right: 0;
    }

  }
}

.content-form__inner-success {
  .content-form__title {
    letter-spacing: 0.4px;
    margin-bottom: 30px;
  }
  .content-form__input-wrapper{
    margin-bottom: 30px;
  }
  .content-form__checkbox {
    width: 35px;
    height: 20px;
    margin-right: 5px;
  }
  .content-form__checkbox-list-label{
    font-size: 12px;
  }
}

//Four-page
.content-form__thanks-page {
  position: absolute;
  left: 50px;
  top: 80px;
  @media (max-width: $sm) {
    position: absolute;
    top: 50%; left: 50%;
    transform: translate(-50%,-50%);
  }
  .content-form__title {
    width: 320px;
    @media (max-width: $sm) {
      width: 300px;
    }
  }
}
// body line
.body-line {
  position: absolute;
  bottom: 55px;
  left: 120px;
  width: 240px;
  height: 18px;
  border-radius: 2px;
  border: 3px solid $darken-yellow-color;
  @media (max-width: $md) {
    position: absolute;
    bottom: 13%; left: 50%;
    transform: translate(-74%,-50%);
  }
  @media (max-width: $sm) {
    position: absolute;
    bottom: 7%; left: 50%;
    transform: translate(-74%,-50%);
    width: 200px;
  }

  &__indicator {
    height: 100%;
    width: 33.333%;
    background-color: red;
    margin-bottom: 5px;
  }
}

.body-line__text {
  transform: translate(5px, -30px);
  @media (max-width: $sm) {
    transform: translate(5px, -25px);
  }

  p {
    color: red;
    font-size: 17.5px;
    white-space: nowrap;
    @media (max-width: $sm) {
      font-size: 15.5px;
    }

    span {
      font-size: 50px;
      font-weight: 700;
      @media (max-width: $sm) {
        font-size: 42px;
      }
    }
  }
}
